<!--  -->
<template>
  <div>
    <van-sticky>
      <van-nav-bar
        title="我的佣金"
        left-arrow
        @click-left="onClickLeft"
        right-text="提现"
        @click-right="toWithdraw"
      />
    </van-sticky>
    <div class="creditTop">
      <span class="haveCredit">可用佣金 {{ topValue }}</span>
      <span class="rightEl tips">推荐好友购买可获得佣金奖励</span>
    </div>
    <!-- 主体内容 -->
    <div class="creditCon">
      <div class="conTitle">佣金明细</div>
      <!-- 列表 -->
      <div class="noData" v-if="commissionList.length == 0">
        暂无数据!
      </div>
      <div class="conWrap">
        <div class="creditItem" v-for="item in commissionList">
          <span class="itemText">{{ item.bill_type }}</span>
          <div class="rightEl itemBtm">
            <div class="itemPrice" :class="item.type == 1 ? '' : 'reduceNum'">
              {{ item.type == 1 ? "+" : "-" }}{{ item.money }}
            </div>
            <div class="itemTime">{{ item.create_time }}</div>
          </div>
        </div>
      </div>
    </div>
    <!-- 提现弹窗 -->
    <van-dialog
      v-model="show"
      title="佣金提现"
      show-cancel-button
      confirm-button-color="#DE4F53"
      :before-close="submitCommission"
    >
      <van-cell-group>
        <van-field v-model="topValue" label="可提现佣金" disabled />
        <van-field
          v-model="commissionInput"
          placeholder="请输入提现佣金"
          label="提现佣金"
        />
        <van-field
          v-model="realName"
          placeholder="请输入您的真实姓名"
          label="真实姓名"
        />
      </van-cell-group>
    </van-dialog>
    <!-- 获取邀请码 -->
    <button class="getCode" @click="$router.push('myInvite')">
      获取邀请码
    </button>
  </div>
</template>

<script>
import { myCommission, withdraw } from "@/api/personal";
export default {
  data() {
    return {
      show: false,
      topValue: "",
      commissionList: [],
      commissionInput: "",
      realName: ""
    };
  },
  created() {
    this.getList();
  },
  methods: {
    getList() {
      myCommission().then(res => {
        this.topValue = res.commission;
        this.commissionList = res.data;
      });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    toWithdraw() {
      this.show = true;
    },
    submitCommission(action, done) {
      if (action == "confirm") {
        if (!this.commissionInput || !this.realName) {
          this.$toast("请输入完整信息!");
          done(false);
        } else if (Number(this.commissionInput) > Number(this.topValue)) {
          this.$toast("要提现的佣金不能大于拥有的佣金数!");
          done(false);
        } else {
          withdraw(this.commissionInput, this.realName).then(res => {
            this.$toast(res.msg);
            this.commissionInput = "";
            this.realName = "";
            this.show = false;
          });
          done();
        }
      } else {
        this.commissionInput = "";
        done();
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.creditTop {
  overflow-x: hidden;
  width: 100%;
  height: 115px;
  background: url(../../assets/imgs/personal/teamBg.png) 0 0 no-repeat;
  background-size: 100% 100%;
  padding: 25px 20px 0;
  box-sizing: border-box;
  font-size: 18px;
  color: #fff;
  box-sizing: border-box;
}
.creditCon {
  margin-top: -50px;
  background: #fff;
  width: 100%;
  height: 100%;
  border-radius: 9.5px 9.5px 0 0;
}
.conTitle {
  color: #333;
  font-size: 18px;
  padding: 12px 0 5px 17px;
}
.conWrap {
  .creditItem {
    margin: 0 15px;
    padding: 19px 0 8.5px;
    font-size: 13px;
    overflow: hidden;
    border-bottom: 1px solid #bfbfbf;
    color: #333;
    font-size: 13px;
    .itemBtm {
      text-align: right;
    }
    .itemTime {
      color: #999;
      font-size: 11px;
      margin-top: 15px;
    }
  }
}
.reduceNum {
  color: #fd5202;
}
.van-nav-bar__text {
  color: #333;
}
.tips {
  font-size: 12px;
  margin-top: 4px;
}
.getCode {
  position: fixed;
  z-index: 100;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #de4f53;
  height: 40px;
  line-height: 40px;
  border: 0 none;
  font-size:15px;
  color:#fff;
}
</style>
